/* media breakpoint mixin */
.tabs {
  background: #fff;
  border-radius: 32px;
  padding: 8px;
}

@media only screen and (min-width: 1000px) {
  .tabs {
    display: flex;
    border-radius: 72px;
  }
}

@media only screen and (max-width: 1000px) {
  .tabs, .tabs__btn {
    width: 100%;
  }
}

.tabs__btn {
  display: flex;
  gap: 8px;
  padding: 10px 24px;
  border-radius: 40px;
  border: 1px solid rgba(255, 255, 255, 0);
  font-weight: 500;
  line-height: 1.14;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: border-color;
}

@media only screen and (max-width: 1000px) {
  .tabs__btn {
    justify-content: center;
  }
}

@media (any-hover: hover) {
  .tabs__btn:hover {
    border-color: #4C57FB;
  }
}

.tabs__btn svg {
  flex-shrink: 0;
}

.tabs__btn--active {
  border-color: #4C57FB;
  background: #4C57FB;
  color: #fff;
}

.card {
  padding: 24px;
  padding-bottom: 0px;
  border-radius: 16px;
  box-shadow: 0 8px 24px 0 rgba(81, 85, 195, 0.08);
  background: #fff;
}

.card__header {
  display: flex;
  align-items: flex-start;
  gap: 8px;
}

.card__title {
  flex-grow: 1;
}

.card__content {
  margin-top: 16px;
}

.card__list {
  padding-top: 16px;
  padding-bottom: 5px;
  max-height: 136px;
  overflow: auto;
  padding-left: 25px;
}

.card__item {
  padding: 16px 0px;
  font-size: 14px;
  line-height: 1.14;
  position: relative;
}

.card__item:after, .card__item:before {
  position: absolute;
  left: calc(0px - 16px);
  height: 1px;
  width: calc(100% + 16px);
  background: #EFF2F5;
}

.card__item:after {
  content: '';
  bottom: 0px;
}

.card__item:first-child:before {
  top: 0px;
  content: '';
}

.dashboard {
  padding: 32px 0px;
}

@media only screen and (min-width: 1000px) {
  .dashboard {
    padding: 64px 0px;
  }
}

.dashboard__row {
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 1000px) {
  .dashboard__row {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

@media only screen and (min-width: 1000px) {
  .dashboard__title-wrapper {
    max-width: 412px;
  }
}

.dashboard__subtitle {
  margin-top: 8px;
}

@media only screen and (max-width: 1000px) {
  .dashboard__tabs-wrapper {
    margin-top: 20px;
  }
}

.dashboard__grid {
  margin-top: 20px;
}

@media only screen and (min-width: 768px) {
  .dashboard__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }
}

@media only screen and (min-width: 1200px) {
  .dashboard__grid {
    margin-top: 32px;
    gap: 32px;
    grid-template-columns: repeat(4, 1fr);
  }
}

@media only screen and (max-width: 768px) {
  .dashboard__card:not(:first-child) {
    margin-top: 20px;
  }
}
